
// imports
import { mapState, mapGetters } from 'vuex'

export default {
  head: {
    title: 'Species View'
  },

  auth: false,
  layout: 'landing',

  data: () => ({
    bcrumbs: [
      {
        text: 'Home',
        to: '/',
      }, {
        text: 'Species',
        to: '/species',
        exact: true
      }, {
        text: 'View',
        to: null,
        active: true
      }
    ]
  }),

  computed: {
    ...mapGetters({
      specie: 'species/getSpecie'
    })
  },

  mounted () {
    this.initialize()
  },

  methods: {
    async initialize () {
      await this.$store.dispatch('species/fetchSpecie', { id: this.$route.params.id })
    }
  }
}

